.canvas {
	width: 100%;
	height: 100%;
}

div.column {
	height: 100%;
	width: 68.1%;
	font-family: 'Josefin Sans', sans-serif;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.loginSection {
	position: relative;
	border-radius: 20px;
}

.frame {
	width: 68.1%;
	background: #333;
	border-radius: 20px;
	margin: 20px 0;
}

div.initialiseButton {
	display: grid;
	height: 100%;
	width: 100%;
	min-height: 150px;
	overflow: hidden;
}

div.forgottenContainer {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	box-sizing: border-box;
	height: 100%;	
}

div.forgotten {
	background-color: #1a1a1a;
	color: grey;
	display: flex;
	justify-content: center;
	align-items: center;
	box-sizing: border-box;
	height: 100%;
	font-size: 16px;
}

.form {
	display: grid;
	grid-template-rows: repeat(4, 1fr);
	height: 100%;
	width: 100%;
}

.form > .input {
	font-family: 'Josefin Sans', sans-serif;
	height: 100%;
	width: 100%;
	overflow: hidden;
	font-size: 16px;
	padding: 0px;
	margin: 0px;
	box-sizing: border-box;
	padding: 10px;
	border: 1px solid lightgrey;
}

input.submit {
	font-family: 'Josefin Sans', sans-serif;
	height: 100%;
	width: 100%;
	background-color: darkgreen;
	justify-content: center;
	padding: 10px;
	margin: 0px;
	box-sizing: border-box;
	border: none;
	color: white;
	-webkit-transition: all 0.68s ease-out;
	-moz-transition: all 0.68s ease-out;
	 -ms-transition: all 0.68s ease-out;
	  -o-transition: all 0.68s ease-out;
		 transition: all 0.68s ease-out;
}

.loginBackground {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-template-rows: 100%;
	align-items: center;
}

.foreground {
	display: flex;
	position: absolute;
	width: 66.7%;
	height: 100%;
	background-color: transparent;
	opacity: 1;
}

.loginForeground input.submit {
	border-bottom-left-radius: 20px;
	-webkit-transition: all 0.68s ease-out;
	-moz-transition: all 0.68s ease-out;
	 -ms-transition: all 0.68s ease-out;
	  -o-transition: all 0.68s ease-out;
		 transition: all 0.68s ease-out;
}
.registerForeground input.submit {
	border-bottom-right-radius: 20px;
	-webkit-transition: all 0.68s ease-out;
	-moz-transition: all 0.68s ease-out;
	 -ms-transition: all 0.68s ease-out;
	  -o-transition: all 0.68s ease-out;
		 transition: all 0.68s ease-out;
}


.loginForeground .form .input:first-child {
	border-top-left-radius: 20px;
	-webkit-transition: all 0.68s ease-out;
	-moz-transition: all 0.68s ease-out;
	 -ms-transition: all 0.68s ease-out;
	  -o-transition: all 0.68s ease-out;
		 transition: all 0.68s ease-out;
}
.registerForeground .form .input:first-child {
	border-top-right-radius: 20px;
	-webkit-transition: all 0.68s ease-out;
	-moz-transition: all 0.68s ease-out;
	 -ms-transition: all 0.68s ease-out;
	  -o-transition: all 0.68s ease-out;
		 transition: all 0.68s ease-out;
}

.loginForeground {
	overflow: hidden;
	left: 0px;
	-webkit-transition: all 0.68s ease-out;
	-moz-transition: all 0.68s ease-out;
	 -ms-transition: all 0.68s ease-out;
	  -o-transition: all 0.68s ease-out;
		 transition: all 0.68s ease-out;
}

.registerForeground {
	left: 33.3%;
	-webkit-transition: all 0.68s ease-out;
	-moz-transition: all 0.68s ease-out;
	 -ms-transition: all 0.68s ease-out;
	  -o-transition: all 0.68s ease-out;
		 transition: all 0.68s ease-out;
}

div.flip {
	display: flex;
	flex-direction: column;
	border-radius: 0px;
}

.flip > div {
    margin: 2px 0;
}

div.messageBar {
	display: flex;
	width: 100%;
    padding: 5px 0 5px 5px;
    box-sizing: border-box;
    border: 2px #252526 solid 0.0;
	box-shadow: none;
	justify-content: center;
	color: red;
}

@keyframes fade-in {
	from {
		opacity: 0.0;
	}
	to {
		opacity: 1.0;
	}
}

/* CROWN */


div.logo {
	width: 50%;
	max-width: 200px;
	display: grid;
	position: relative;
	grid-template: 100% / 100%;
}

img.staticLogo {
	width: 100%;
	position: relative;
	grid-column: 1 / span 1;
	grid-row: 1 / span 1;
}

img.loadingGif {
    position: absolute;
    width: 66.6%;
    max-width: 200px;
    transform: rotate(-33deg);
    opacity: 1;
    left: 6%;
    top: 15%;
	position: relative;
	grid-column: 1 / span 1;
	grid-row: 1 / span 1;
}

img.crownOutline {
    position: absolute;
    width: 71%;
    max-width: 213px;
    transform: rotate(-32deg);
    opacity: 1;
    z-index: 1;
    left: 5%;
    top: 13%;
	position: relative;
	grid-column: 1 / span 1;
	grid-row: 1 / span 1;
}

@media screen {
	.logo {
		animation: fade-in 1 0.5s linear;
	};
	.loadingLine {
		animation: grow-right infinite 0.68s linear;
	}
}

@keyframes fade-in {
	from {
		opacity: 0
	}
	to {
		opacity: 1
	}
}