.container {
    width: 100%;
    max-width: 1180px;
	margin: 0 20px 50px;
	box-sizing: border-box;
	overflow: hidden;

	display: grid;
	column-gap: 50px;
	row-gap: 25px;
	grid-template-columns: 1fr 1fr;
	font-family: 'Josefin Sans', sans-serif;
}

.section {
	display: grid;
	overflow: hidden;
}

@media only screen and (max-width: 700px) {
	div.container {
		grid-template-columns: 100%;
	}
	div.section {
		grid-column: unset;
		grid-row: unset;
	}
	div.title {
		grid-column: unset;
	}
}


.title {
	grid-column: 1 / span 2;
	grid-row: 1 / span 1;
	padding: 10px;
}
.title h1 {
	margin: 20px 0;
}
.title h3 {
	margin: 0;
}

.announcements {
	grid-column: 1 / span 1;
	grid-row: 2 / span 1;
}

.tableAnnounce {
	width: 100%;
	overflow: hidden;
	border-radius: 40px;
	margin-bottom: 50px;
}

@media only screen and (max-width: 700px) {
	.section > table {
		margin-bottom: 0px;
	}
	.section > table thead {
		display: table-header-group;
	}
	.section > table th {
		width: 100%;
		display: flex;
	}
	.section > table td {
		width: 100%;
		display: flex;
		text-align: center;
	}
}

.yourTournaments {
	grid-column: 1 / span 2;
	grid-row: 3 / span 1;
}

.tournaments {
	grid-column: 2 / span 1;
	grid-row: 2 / span 1;
}

.tournaments > table, .yourTournaments > table {
	overflow: hidden;
	border-radius: 40px;
	margin-bottom: 0;
}

.sectionHeader {
	background-color: skyblue;
	justify-content: center;
	text-align: center;
	box-sizing: border-box;
	border: white 1px solid;
}

tr {
	overflow: hidden;
}

.notificationRow {
	overflow-x: hidden;
	white-space: nowrap;
}

.notificationRow > * {
	padding: 0 5px;
}

.notificationRow > td > div {
	width: 100%;
	display: grid;
	grid-template-columns: 5% auto 1fr 5%;
	column-gap: 10px;
	overflow: hidden;
	align-items: center;
}

.notificationRow > td > div > div, .notificationRow > td > div > a {
	display: flex;
	width: 100%;
	overflow: hidden;
	align-items: center;
}

.expando {
	cursor: pointer;
}

:global(.download-table) > tbody .rowLink:hover {
	background-color: #43853d;
	color: white;
}

:global(.download-table) > tbody .rowLink:hover > * > * {
	color: white;
}

:not(:global(#profileTournaments)).tournamentLink {
	width: 100%;
	display: grid;
	grid-template-columns: auto 5%;
	grid-auto-flow: column;
	align-items: center;
	justify-content: center;
}

.notificationRow > td > div:hover, .notificationRow > td > div:hover > a.iconLink {
    color: #fff;
	background-color: #43853d;
	cursor: pointer;
}

.connected, .disconnected {
	display: flex;
	align-items: center;
}

.connected > svg {
	color: #43853d;
	height: 1em;
	padding: 0 5px;
}

.disconnected {
	color: indianred;
	height: 1em;
	padding: 0 5px;
}

.container .new {
	display: grid;
	grid-template-columns: 95% 5%;
	width: 100%;
	align-items: center;
	border-radius: 40px;
	padding: 1px;
}