.stage {
	display: grid;
	column-gap: 20px;
	row-gap: 20px;
	grid-template-columns: 30.8% repeat(2, 1fr);
	grid-template-rows: 1fr auto;
	grid-template-areas:
		'image name name'
		'image website rules'
		'image modes modes'
}

@media only screen and (max-width: 700px) {
	.stage {
		grid-template-columns: 1fr;
		grid-template-rows: auto;
		grid-template-areas:
			'name'
			'image'
			'website'
			'rules'
			'modes'
	}
}

.nameContainer {
	grid-area: name;
	display: flex;
	align-items: center;
}

.name {
	background-color: rgba(150, 150, 150, 0.68);
	border-radius: 15px;
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	font-weight: bold;
	font-size: 24px;
	padding: 10px;
}

.profilePicture {
	grid-area: image;
	border-radius: 15px;
	overflow: hidden;
	display: flex;
	justify-content: center;
}

.profilePicture > img {
	max-width: unset;
}

.buttonContainer {
	display: grid;
	grid-auto-columns: 1fr;
	grid-auto-flow: column;
	column-gap: 20px;
}

a.website {
	grid-area: website;
}
a.rules {
	grid-area: rules;
}
a.website, a.rules {	
	min-width: 150px;
	border-radius: 10px;
	align-items: center;
	display: flex;
	flex-direction: row;
	border: none;
	padding: 10px;
	background-color: skyblue;
	justify-content: center;
	text-align: center;
	cursor: pointer;
}
a.website:hover, a.rules:hover {
	background-color: #43853d;
}

a.website > text {
	text-align: left;
}

a.website > svg, a.rules > svg {
	margin: 0 10px;
}

.defaultProfilePicture {
	box-sizing: content-box;
	display: flex;
	width: 100%;
	min-height: 15vw;
	justify-content: center;
	align-items: center;
	font-size: xx-large;
	font-family: 'Josefin Sans', sans-serif;
}

.defaultProfilePicture > text {
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
}