.chartist-tooltip {
	background-color: palegoldenrod;
	z-index: 1;
	display: none; /*
	flex-direction: column;
	justify-content: center;
	justify-items: center;*/
	font-family: 'Josefin Sans';
	border-radius: 20px;
	padding: 5px;

	position: absolute;
	margin-bottom: 160px;/*
	margin-left: 140px;*/
}

div.tooltip-show {
	display: flex;
}

.chartContainer {
	display: grid;
	grid-template-columns: 100%;
	overflow: hidden;
}

@media screen and (max-width: 700px) {
	.chartContainer {
		grid-auto-flow: row;
	}
	div.chartKey {
		grid-column: 1 / span 2;
		grid-row: 2 / span 1;
		text-align: center;
	}
}

.ct-chart {
	grid-column: 1 / span 2;
	grid-row: 1 / span 1;
	font-family: 'Josefin Sans', sans-serif;
}

.gauge .ct-chart > svg {
	padding-bottom: 20px;
}

.chartKey {
	grid-column: 1 / span 1;
	grid-row: 1 / span 1;
	position: relative;
	height: calc(80vh - 15px);
	display: grid;
	font-family: 'Josefin Sans';
	position: relative;
	margin-left: 40px;
	pointer-events: none;
}

.playerLabel {
	display: flex;
	flex-direction: row;
	align-items: center;
}

.playerLabel hr {
	border: dashed 0.5px;
	margin: 0 10px;
	height: 0.5px;
	opacity: 0.3;
	position: relative;
	z-index: -1;
}

@media screen and (max-width: 700px) {
	.playerLabel hr {
		display: none;
	}
}

[*|meta='placeholder'] {
	display: none;
}

.gauge .ct-axis-title, .ct-label {
	color: black;
	font-family: 'Josefin Sans', sans-serif;
}

.ct-label.ct-vertical.ct-start {
	overflow: visible;
}

.ct-label.ct-vertical.ct-start {
	overflow: hidden;
}

.chartArrow {
	align-items: center;
	margin: 5px 0;
	line-height: 100%;
	height: 100%;
}

.chartArrow > div {
	display: inline-block;
	width: 25px;
	justify-content: right;
	text-align: right;
	margin-right: 5px;
}

.chartArrow > svg {
	height: 100%;
	position: relative;
	top: 5px;
	margin-right: 5px;
	width: 25px;
}

.chartPositive > svg {
	color: green;
}

.chartNegative > svg {
	color: red;
}

.chartNeutral > svg {
	color: grey;
}

/* PIE */

.gauge > div {
	max-height: 200px;
	margin-bottom: 20px;
}
.gauge div.ct-chart > svg {
	overflow: visible;
	padding-top: 20px;
}

.gauge .ct-label {
	margin: 50px;
}


.ct-slice-donut-solid[*|meta='confirmed'] {
	fill: gold
}
.ct-slice-donut-solid[*|meta='finished'] {
	fill: #43853d
}
.ct-slice-donut-solid[*|meta='live'] {
	fill: rgb(73, 73, 255);
}
.ct-slice-donut-solid[*|meta='registered'] {
	fill: turquoise
}
.ct-slice-donut-solid[*|meta='scheduled'] {
	fill: turquoise;
}
.ct-slice-donut-solid[*|meta='pending'] {
	fill: grey
}
.ct-slice-donut-solid[*|meta='aborted'] {
	fill: indianred
}

.gauge .ct-label {
	font-size: 24px;
}